<template title="加盟商资料修改">
  <div class="ProfileDetail">
    <mt-field label="加盟商名称" v-model="name" placeholder="请输入名称"></mt-field>
    <div class="join-p btn">
      <mt-button type="primary" size="large" @click="changeName()">
        确认更换
      </mt-button>
    </div>
  </div>
</template>
<script>
export default {
  data() {
    return {
      name: ''
    }
  },
  methods: {
    async changeName() {
      const result = await this.$api.modifyName(this.name)
      this.$router.go(-1)

    }
  },
  mounted() {
    this.name = this.$route.query.name;
  }
}
</script>

<style >
.btn {
  padding-top: 40px;
}
</style>



