<template>
  <div class="city">
    <div class="topbanner">
      <div class="icon"><img src="~@/assets/icon_city.png" width="80" alt=""></div>
      <h1>你的城市你做主</h1>
      <p>您已经正式成为加盟商的一员，
        <br> 每一个地区，都是一片蓝海，等待你去开拓。</p>
      <p>选择你熟悉的城市，
        <br> 为商家提供增值服务，找到你的商机。</p>
    </div>
    <div class="chooseCity" @click="openPicker()">
      {{selectedCity == null ? '省份/城市' : selectedProvince + '/' + selectedCity.city}}
      <i class="icon"></i>
    </div>
    <div>
      <mt-popup v-model="pickerVisible" position="bottom" class="mint-datetime">
        <mt-picker ref="regionPicker" class="mint-datetime-picker" :slots="regionSlots" :visibleItemCount="7" valueKey="city" @change="onValuesChange" show-toolbar>
          <span class="mint-datetime-action mint-datetime-cancel" @click="cancelSelect">取消</span>
          <span class="mint-datetime-action mint-datetime-confirm" @click="sureSelect">确定</span>
        </mt-picker>
      </mt-popup>
    </div>
    <div class="page-button">
      <mt-button :type="selectedCity == null ? 'default' : 'primary'" :disabled="selectedCity == null" size="large" @click="confirm()">
        继续
      </mt-button>
    </div>
  </div>
</template>

<script>
  import { Toast } from 'mint-ui'
  let regions = []

  export default {
    data: () => ({
      pickerVisible: false,
      regionSlots: [{
          flex: 1,
          values: [],
          textAlign: 'center'
        },
        {
          divider: true,
          content: '-'
        },
        {
          flex: 1,
          values: [],
          textAlign: 'center'
        }
      ],
      currentValue: null,
      selectedProvince: null,
      selectedCity: null
    }),
    methods: {
      openPicker() {
        const provinces = Object.keys(regions)
        this.regionSlots[0].values = provinces

        const citiesInFirstProvince = regions[Object.keys(regions)[0]]
        this.regionSlots[2].values = citiesInFirstProvince

        this.pickerVisible = true
      },
      cancelSelect () {
        this.pickerVisible = false
      },
      sureSelect () {
        const province = this.currentValue[0]
        const cities = regions[province]
        this.$refs.regionPicker.setSlotValues(1, cities)
        this.selectedProvince = province
        this.selectedCity = this.$refs.regionPicker.values[1]
        this.pickerVisible = false
      },
      onValuesChange(picker, values) {
        this.currentValue = values
        const province = values[0]
        const cities = regions[province]
        picker.setSlotValues(1, cities)
        // this.selectedProvince = province
        // this.selectedCity = picker.values[1]
      },
      confirm() {
        if (this.$city.isHaveCity(this.selectedCity.regionId)) {
          Toast({message: '您已经开通了此区域'})
        } else {
          this.$city.current = this.selectedCity
          this.$router.push({
            name: 'purchase'
          })
        }

      }
    },
    async mounted() {
      regions = await this.$api.getRegions()
    }
  }
</script>

<style lang="scss">
  // @import "~@/style/city.scss"
</style>



