import Vue from 'vue'
import Router from 'vue-router'
import {
  Landing,
  JoinLetter,
  Login,
  ChooseRegion,
  Portal,
  Licenses,
  Purchase,
  PurchaseConfirm,
  LicenseDetail,
  Authorize,
  AuthorizeConfirm,
  Customers,
  CustomerDetail,
  AddCustomer,
  Mine,
  Profile,
  ProfileDetail,
  TradeLogs,
  TradeLogDetail,
  Messages,
  MessageDetail,
  UnbindMobile,
  ChangeMobile,
  Repurchase
} from '@/pages'

Vue.use(Router)

var router = new Router({
  routes: [{
    path: '/',
    name: 'landing',
    component: Landing,
    meta: {
      anonymous: true,
      title: '壹向加盟商'
    }
  },
  {
    path: '/join-letter',
    name: 'join-letter',
    component: JoinLetter,
    meta: {
      anonymous: true,
      title: '加盟介绍'
    }
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
    meta: {
      anonymous: true
    }
  },
  {
    path: '/choose-region',
    name: 'choose-region',
    component: ChooseRegion,
    meta: {
      title: '选择加盟区域'
    }
  },
  {
    path: '/purchase',
    name: 'purchase',
    component: Purchase,
    meta: {
      title: '购买'
    }
  },
  {
    path: '/Repurchase',
    name: 'Repurchase',
    component: Repurchase,
    meta: {
      title: '续费'
    }
  },
  {
    path: '/purchase-confirm',
    name: 'purchase-confirm',
    component: PurchaseConfirm,
    meta: {
      title: '支付确认'
    }
  },
  {
    path: '/portal',
    name: 'portal',
    component: Portal,
    children: [{
      path: '/licenses',
      name: 'licenses',
      component: Licenses,
      meta: {
        label: '服务',
        icon: 'icon-service',
        title: '我的服务'
      }
    },
    {
      path: '/customers',
      name: 'customers',
      component: Customers,
      meta: {
        label: '客户',
        icon: 'icon-customer',
        title: '我的客户'
      }
    },
    {
      path: '/mine',
      name: 'mine',
      component: Mine,
      meta: {
        label: '我的',
        icon: 'icon-mine',
        title: '我的'
      }
    }
    ]
  },
  {
    path: '/license/:id',
    name: 'license-detail',
    component: LicenseDetail
  },
  {
    path: '/authorize/:id',
    name: 'authorize',
    component: Authorize
  },
  {
    path: '/authorize-confirm',
    name: 'authorize-confirm',
    component: AuthorizeConfirm
  },
  {
    path: '/customer/:id',
    name: 'customer-detail',
    component: CustomerDetail,
    meta: {
      title: '客户详情'
    }
  },
  {
    path: '/add-customer',
    name: 'add-customer',
    component: AddCustomer,
    meta: {
      title: '添加客户'
    }
  },
  {
    path: '/profile',
    name: 'profile',
    component: Profile,
    meta: {
      title: '我的信息'
    }
  },
  {
    path: '/profile-detail',
    name: 'profile-detail',
    component: ProfileDetail,
    meta: {
      title: '我的信息'
    }
  },
  {
    path: '/unbind-mobile',
    name: 'unbind-mobile',
    component: UnbindMobile,
    meta: {
      title: '解绑手机'
    }
  },
  {
    path: '/change-mobile',
    name: 'change-mobile',
    component: ChangeMobile,
    meta: {
      title: '修改手机'
    }
  },
  {
    path: '/trade-logs',
    name: 'trade-logs',
    component: TradeLogs,
    meta: {
      title: '交易记录'
    }
  },
  {
    path: '/trade-log/:id',
    name: 'trade-log-detail',
    component: TradeLogDetail,
    meta: {
      title: '交易详情'
    }
  },
  {
    path: '/messages',
    name: 'messages',
    component: Messages,
    meta: {
      title: '消息列表'
    }
  },
  {
    path: '/messages/:id',
    name: 'message-detail',
    component: MessageDetail,
    meta: {
      title: '消息详情'
    }
  }
  ]
})

// 检查是否已登录
router.beforeEach((to, from, next) => {
  setDocumentTitle(to.meta.title || '壹向·加盟商')
  // 路由不需要登录
  if (to.matched.every(record => record.meta.anonymous)) {
    next()
    return
  }

  // 已经登录
  if (router.app.$auth.checkLoggedIn()) {
    next()
    return
  }

  // 未登录过
  next({
    path: '/login',
    query: {
      redirect: to.fullPath
    }
  })
})

/**
 * 兼容微信设置页面的title
 */
let setDocumentTitle = function (title) {
  document.title = title
  let ua = navigator.userAgent
  if (/\bMicroMessenger\/([\d\.]+)/.test(ua) && /ip(hone|od|ad)/i.test(ua)) {
    var i = document.createElement('iframe')
    i.src = '/favicon.ico'
    i.style.display = 'none'
    i.onload = function () {
      setTimeout(function () {
        i.remove()
      }, 9)
    }
    document.body.appendChild(i)
  }
}
export default router



