<template title="交易记录详情">
  <div class="TradeLogDetail">
    <div class="page-part">
      <mt-cell title="金额" class="orderPrice">
        <span v-if="order.type !== 'allianceBuy'" class="income join-p-tb">+{{order.paymoney | money}}</span>
        <span v-if="order.type === 'allianceBuy'" class="cost">-{{order.paymoney | money}}</span>
      </mt-cell>
      <mt-cell title="付款详情" :value="payFor"></mt-cell>
      <mt-cell title="当前状态" v-if="order.status != 0" :value="orderStatus(order.status)"></mt-cell>
      <mt-cell title="付款时间" :value="order.addtime | date('yyyy-MM-dd HH:MM:ss')"></mt-cell>
      <mt-cell title="交易单号" :value="order.orderid"></mt-cell>
    </div>
    <div class="list-title join-p-lr">
      <i class="icon"></i>订单明细</div>
      <div class="servicelist" v-if="order.type === 'allianceBuy'">
        <mt-cell  v-for="(item,index) in detail.buyContext" :key="index"   :title="item.serviceName" :value="`${item.quantity || item.newQuantity}个授权 | ${item.autoEndDate?item.autoEndDate+'到期':item.month+'个月'}`">
        </mt-cell>

      </div>
      <div class="priceList" v-if="order.type === 'allianceBuy'">
        <mt-cell  v-for="(packge,index) in detail.packgeContext" :key="index" :title="`${packge.packgeTime || packge.month}个月 | ${packge.packgeName || packge.lisenceCount+'授权'}`">
          <span class="amount">x1</span>
          <span class="unitPrice">¥{{packge.packgeMoney | money}}</span>
        </mt-cell>
      </div>

    <div class="priceList" v-if="order.type === 'storeBuy'">
      <mt-cell v-for="(item,index) in detail" :key="index" :title="item.title">
        <!--<span class="amount">x1</span>-->
        <span class="unitPrice">¥{{item.paymoney | money}}</span>
      </mt-cell>

    </div>

  </div>
</template>
<script>
export default {
  data() {
    return {
      order: {},
      detail:{}
    }
  },
  computed: {
    payFor () {
      if (Object.keys(this.order).length === 0) {
        return ''
      }
      if (this.order.title.indexOf('续费服务') > -1) {
        return this.order.city + '续费服务'
      } else if (this.order.title.indexOf('购买服务')) {
        return this.order.city + '开通服务'
      }
      return this.order.title || ''
    }
  },
  methods: {
    orderStatus (type) {
      if (type === 'new') {
        return '未付款'
      } else if (type === 'timeout') {
        return '支付超时'
      } else if (type === 'commit') {
        return '支付成功'
      } else {
        return '未知状态'
      }
    }
  },
  async mounted() {
    let order = this.$route.params
    this.order = order
    const result = await this.$api.getOrderDetail(order.orderid, order.type)
    this.detail = result
  }
}
</script>
<style lang="scss">

</style>



