<template>
  <div class="dropdown-wrap">
    <h5 class="label">
      {{label}}
    </h5>
    <div class="mint-cell dropdown-title" @click=" items.length > 0 ? visible = !visible:''">
      <div class="mint-cell-wrapper">
        <div class="mint-cell-title">
          <h2 class="Name">
            {{selected[displayField]}}
            <i class="icon" v-if="items.length > 0"></i>
          </h2>
        </div>
        <div class="mint-cell-value">
          <slot></slot>
        </div>
      </div>
    </div>
    <div class="dropdown-list" v-if="visible">
      <mt-cell
        v-for="item in items"
        :key="item[idField]"
        :title="item[displayField]"
        :class="{'selected': item[idField] === selected[idField]}"
        @click.native="itemClick(item)">
        <i
          v-if="item[idField] === selected[idField]"
          class="icon icon-tick">
        </i>
      </mt-cell>
    </div>
  </div>
</template>

<script>
export default {
  props: {
    label: String,
    items: Array,
    selected: Object,
    idField: String,
    displayField: String
  },
  data: () => ({
    visible: false
  }),
  methods: {
    itemClick(item) {
      this.$emit('select', item)
      this.visible = false
    }
  }
}
</script>



