<template title="单个客户服务详情">
  <div class="CustomerDetail btn-p-b">
    <div class="storeInfo">
      <div class="mint-cell">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title">
            <h3>
              {{ shopInfo.brandname }} {{ shopInfo.storename }}
            </h3>
            <p>{{ shopInfo.address }}</p>
            <p>
              <a class="storeMobile" :href="`tel:${shopInfo.mobile}`">{{shopInfo.mobile}}</a>
            </p>
            <p>
              <a class="storeMobile" :href="`tel:${shopInfo.tel}`">{{shopInfo.tel}}</a>
            </p>
          </div>
        </div>
      </div>
    </div>
    <div class="list-wrap">
      <div class="list-title">
        <i class="icon"></i>门店服务使用状况</div>
      <div class="list-time">
        <div class="item" :class="{ 'warning': license.status === 'WARNING' }" v-for="(license,index) in licenses" v-if="license.inUse" @click="config(license)" :key="index">
          <h4>{{license.name}}</h4>
          <AuthorizeStatus class="authorizeStatus" :authorize="authorize" v-if="license.canBuyLisences && license.canBuyMonth">
          </AuthorizeStatus>
          <p class="canSale" v-if="license.canBuyLisences && license.canBuyMonth">
            可售卖时长：{{license.canBuyMonth}}个月
          </p>
          <p>
            <span class="date" v-if="license.inUse">
               到期日： {{ license.endDate | date}}
              </span>
            <span class="date" v-else>
                门店未开通该服务
              </span>

            <span class="timeWrap" v-if="license.inUse">
                <i v-for="(i, index) in license.remainingMonth" :key="index" :class="license.status === 'WARNING' ? 'warn' : 'normal'"></i>
                <i v-for="(j, index) in grayDots(license)" :key="index">
                </i>
              </span>
          </p>
        </div>
        <!--TODO: 升级版  -->
        <!-- <div class="item-note">
          <h5>补齐剩余时长对应的差价，即可升级到专业版。</h5>
          <p>基础版剩余132天使用时间，升级需补齐相应差价：230.50元</p>
        </div> -->
      </div>
    </div>
    <div class="footer-btnWrap" v-if="canBuy">
      <mt-button type="primary" size="large" @click="buy()">
        确定
      </mt-button>
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <EditAuthorizePopup :license="licenseInfo" :purchase="authorize" @save="saveAuthorize">
      </EditAuthorizePopup>
    </mt-popup>
     <!-- <div class="footer-btnWrap">
      <mt-button type="primary" size="large" @click="buy">立即开通</mt-button>
    </div> -->
  </div>
</template>

<script>
  import {
    Toast
  } from 'mint-ui'
  import AuthorizeStatus from '@/components/AuthorizeStatus'
  import EditAuthorizePopup from '@/components/EditAuthorizePopup'

  export default {
    components: {
      AuthorizeStatus,
      EditAuthorizePopup
    },
    data: () => ({
      popupVisible: false,
      shopInfo: {},
      licenseInfo: {},
      authorize: null,
      licenses: []
    }),
    computed: {
      canBuy() {
        return this.authorize != null && this.authorize.month > 0
      }
    },
    methods: {
      async requestInfo() {
        const shopId = this.$route.params.id
        const result = await this.$api.getShopLicenses(this.$city.current, {
          id: shopId
        })
        console.log('门店详情')
        console.log(result)
        this.shopInfo = result.storeInfo
        // this.$nextTick(()=>{

        // })
        this.licenses = result.serviceList.filter(item => {
          return item.inUse
        })
        this.licenseInfo = this.licenses[0]
      },
      grayDots(license) {
        return license.canBuyMonth
      },
      saveAuthorize(authorize) {
        // this.$emit('edit', authorize)
        this.popupVisible = false
        this.authorize = authorize
      },
      config(lisence) {
        if (lisence.canBuyLisences && lisence.canBuyMonth) {
          this.popupVisible = true
        }
      },
      async buy() {
        const city = this.$city.current
        let price = this.authorize.month * this.licenseInfo.monthMoney
        const authorize = {
          serviceId: this.authorize.licenseId,
          buyMonth: this.authorize.month
        }
        const result = await this.$api.authorizeShopLicenses(city, this.shopInfo, [authorize], price)
        Toast({
          message: '续费成功'
        })
        this.$router.go(-1)
        // this.authorize = null
        // this.requestInfo()
      }
    },
    async mounted() {
      this.requestInfo()
    }
  }
</script>

<style>
  .canSale {
    font-size: 12px;
    color: red;
  }

  .storeMobile {
    color: #4a90e2;
  }

  .authorizeStatus {
    position: absolute;
    right: 10px;
    top: 10px;
  }


</style>




