<template>
  <div class="list-wrap">
    <div class="list-service">
      <dl class="item" v-for="license in licenses" :key="license.id" @click="openEditPopup(license)" v-if="!isOpen || (isOpen && license.inUse)">
        <dt>
          <h4>{{license.name}}</h4>
          <p v-if="license.canBuyMonth > 0">
            可售时长：{{license.canBuyMonth}}个月
          </p>
          <p class="price">
            {{license.monthMoney | money}}元/月
          </p>
        </dt>
        <AuthorizeStatus v-if="license.canBuyLisences && license.canBuyMonth>0" :authorize="authorize(license.id)">
        </AuthorizeStatus>
      </dl>
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <EditAuthorizePopup :license="editLicense" :purchase="editAuthorize" @save="saveAuthorize">
      </EditAuthorizePopup>
    </mt-popup>
  </div>
</template>

<script>
import AuthorizeStatus from './AuthorizeStatus'
import EditAuthorizePopup from './EditAuthorizePopup'

export default {
  components: {
    AuthorizeStatus, EditAuthorizePopup
  },
  props: {
    licenses: Array,
    authorizes: Array
  },
  computed: {
    isOpen() {
      for (let license of this.licenses) {
        console.log(license)
        if (license.inUse) {
          return true
        }
      }
      return false
    }
  },
  data: () => ({
    popupVisible: false,
    editLicense: null,
    editAuthorize: null,
  }),
  methods: {
    authorize(licenseId) {
      return this.authorizes.find(p => p.licenseId === licenseId)
    },
    openEditPopup(license) {
      if (!license.canBuyMonth || !license.canBuyLisences) {
        return
      }

      const authorize = this.authorizes.find(p => p.licenseId === license.id)

      this.editLicense = license
      this.editAuthorize = authorize
      this.popupVisible = true
    },
    saveAuthorize(authorize) {
      this.$emit('edit', authorize)
      this.popupVisible = false
      this.editLicense = null
      this.editAuthorize = null
    }
  }
}
</script>



