<template title="订单列表">
  <div class="order btn-p-b">
    <div class="join-spaceTitle">
      {{$city.current.city}}
    </div>
    <div class="priceList page-part">
        <div class="list-service" v-if="configs.length == 0">
          <dl class="item" v-for="(purchase,index) in purchases" :key="index">
            <dt>
              <h4>{{purchase.licenseName}}</h4>
              <!--<p>{{purchase.description}}</p>-->
              <p class="">服务用量: {{purchase.quantity}}个授权数 | {{purchase.month}}个月</p>
            </dt>
            <dd class="price">¥{{purchase.price | money}}</dd>
          </dl>
        </div>
        <div class="list-service" v-if="configs.length > 0">
          <dl class="item" >
            <dt>
             <h4>方案详情</h4>
              <p v-for="(packge, index) in packages" :key="index">{{ packge.packgeName }} x{{packge.count}}</p>
            </dt>
            <dd class="price">¥{{reNewPrice | money}}</dd>
          </dl>
        </div>
        <ul class="service-detail">
          <li v-for="(config, index) in configs" :key="index">
            <h4>- {{config.name}}</h4>
            <p>· 续费服务 </p>
            <p>· 追加：{{config.detail.newQuantity}}个授权数 </p>
            <p>· 到期日：{{config.detail.newEndDate | date}}</p>
          </li>
        </ul>
        <!--<mt-cell>
          <span>总价：</span>
          <span class="totalPrice">¥{{totalPrice | money}}</span>
        </mt-cell>-->

    </div>
    <div class="PaymentTypes">
      <mt-cell title="支付方式" value="微信支付"></mt-cell>
    </div>
    <div class="footer-btnWrap">
      <mt-button type="primary" size="large" @click="confirm()">确认支付</mt-button>
    </div>
  </div>
</template>

<script>
  import wx from 'weixin-js-sdk'
  import {
    Toast
  } from 'mint-ui'
  export default {
    data: () => ({
      purchases: [],
      configs: [],
      packages: []
    }),
    computed: {
      totalPrice() {
        if (this.packages == null) {
          return 0
        }
        return this.packages.reduce((s, p) => s + p.realPrice, 0)
      },
      // 续费总价
      reNewPrice () {
        if (this.packages == null) {
          return 0
        }
        return this.packages.reduce((s, p) => s + p.realPrice * p.count, 0)
      }

    },
    methods: {
      async checkOrder(orderId) {
        const data = await this.$api.getOrderStatus(orderId)
        if (data.status === 'commit') {
          Toast({
            message: '付款成功'
          })
          this.$router.push({
            name: 'portal'
          })
        } else {
          this.checkOrder(orderId)
        }
      },
      async confirm() {
        let purchases = this.purchases
        if (this.configs.length === 0) {
          purchases = this.purchases.map(p => ({
            serviceId: p.licenseId,
            month: p.month,
            quantity: p.quantity
          }))
        }

        const result = await this.$api.confirmPurchase(this.$city.current, purchases, this.packages, this.configs.length > 0 ? this.reNewPrice : this.totalPrice)
        let _this = this
        let payResult = {
          // appId:'wxff3552f9ae378e15',
          nonceStr: result.nonceStr, // 支付签名随机串，不长于 32 位
          package: 'prepay_id=' + result.prepay_id, // 统一支付接口返回的prepay_id参数值，提交格式如：prepay_id=***）
          paySign: result.paySign, // 支付签名
          signType: 'MD5', // 签名方式，默认为'SHA1'，使用新版支付需传入'MD5'
          timestamp: result.timestamp, // 支付签名时间戳，注意微信jssdk中的所有使用timestamp字段均为小写。但最新版的支付后台生成签名使用的timeStamp字段名需大写其中的S字符
          success: function(res) {
            Toast({
              message: '付款成功'
            })
            _this.checkOrder(result.ordersId)
          },
          cancel: function(res) {
            _this.$api.cancelOrder(result.ordersId)
          }

        }
        wx.chooseWXPay(payResult)
      }
    },
    mounted() {
      if (this.$route.params.purchases == null  || this.$route.params.packages == null) {
        this.$router.go(-1)
      }

      this.purchases = this.$route.params.purchases || []
      this.packages = this.$route.params.packages || []
      this.configs = this.$route.params.configs || []
    }
  }
</script>

<style>
  body{
    background-color: #f7f7fa;
  }
</style>



