<template title="我的主页">
  <div class="Mine">
    <div class="page-part">
      <mt-cell title="加盟商名称" :value="userInfo.userName || ''" is-link :to="{name:'profile-detail',query:{'name':userInfo.userName || ''}}"></mt-cell>
      <mt-cell title="绑定手机" :value="userInfo.mobile || ''" is-link :to="{path:'/change-mobile',query:{mobile:userInfo.mobile}}"></mt-cell>
    </div>
    <div class="page-part">
      <mt-cell title="交易记录" label="" is-link :to="{path:'/trade-logs'}"></mt-cell>
      <mt-cell title="消息中心" label="" is-link :to="{path:'/messages'}"></mt-cell>
    </div>
  </div>
</template>

<script>

export default {
  data () {
    return {
        userInfo:{}
    }
  },
   async mounted () {
      this.userInfo = await this.$api.getUserInfo()


  }
}
</script>




