<template title="消息列表">
  <div class="messages">
    <div class="list-time">
      <div class="item" v-for="(item,index) in messages" :key="index" @click="goToDetail(item)">
        <h4>
          <!-- tag-redBd tag-yellowBd -->
          <i class="icon-tag tag-blueBd">{{item.notifytype | message}}</i> {{item.detail.brandname +' '+ item.detail.storeName}}</h4>
        <h5>{{item.addtime | date('yyyy-MM-dd HH:MM:ss')}}</h5>
        <p class="join-p-t">{{item.detail.address}}</p>
      </div>
    </div>
    <div v-if="messages.length == 0" class="no-customer fn-center">
      <span style="display: inline-block;">
          <i class="icon"></i>
          <p>暂无任何消息</p>
        </span>
    </div>
  </div>
</template>

<script>
  export default {
    data() {
      return {
        messages: []
      }
    },
    methods: {
      goToDetail(message) {
        this.$router.push({
          name: 'message-detail',
          query: {
            id: message.detail.id
          }
        })
      }
    },
    async mounted() {
      this.messages = await this.$api.getMessages()
    }
  }
</script>



