<template>
<div>
  <mt-popup v-model="visible" position="bottom" class="mint-datetime">
    <mt-picker :slots="dateSlots" @change="onChange" class="mint-datetime-picker" ref="picker" show-toolbar :visibleItemCount="7">
      <span class="mint-datetime-action mint-datetime-cancel" @click="visible = false;$emit('cancel')">{{ cancelText }}</span>
      <span class="mint-datetime-action mint-datetime-confirm" @click="confirm">{{ confirmText }}</span>
    </mt-picker>
  </mt-popup>
</div>

</template>

<script>
export default {
  name: 'license-count-picker',
  props: {
    cancelText: {
      type: String,
      default: '取消'
    },
    confirmText: {
      type: String,
      default: '确定'
    },
    minNum: {
      type: Number,
      default: 0
    },
    maxNum: {
      type: Number,
      default: 0
    }
  },
  computed: {
    dateSlots() {
      let slots = [{
        flex: 1,
        values: ['追加'],
        className: 'slot1'
      },
      {
        flex: 1,
        values: [],
        className: 'slot1'
      },
      {
        flex: 1,
        values: ['个授权'],
        className: 'slot1'
      }]
      let values = []
      for (var index = this.minNum; index <= this.maxNum; index++) {
        values.push(index)
      }
      slots[1].values = values
      return slots
    }
  },
  data() {
    return {
      currentValue: 0,
      visible: false
    }
  },
  methods: {
    onChange (picker, value) {
      this.currentValue = value[0]
    },
    confirm (value) {
      this.visible = false
      console.log(this.currentValue)
      this.$emit('confirm', this.currentValue)
    },
    open () {
      this.visible = true
    },
    close () {
      this.visible = false
    }
  }
}
</script>



