<template>
  <div>
    <div class="header ">
      <i class="icon-tag tag-blueBd">新店入驻</i>
      <h4> {{storeInfo.storename}}</h4>
      <h5 class="time">{{storeInfo.createTime | date('yyyy-MM-dd HH:MM:ss')}}</h5>

    </div>
    <div class="storeInfo">
      <p>{{storeInfo.context}}</p>
      <p>{{storeInfo.address}}</p>

      <p><a :href="`tel:${storeInfo.mobile}`">{{storeInfo.mobile}}</a></p>
      <p><a :href="`tel:${storeInfo.tel}`">{{storeInfo.tel}}</a></p>
    </div>
    <div class="list-title scope-title">
        <i class="icon"></i>可选服务
        <span class="status">{{isBuy?'已购买':'未购买'}}</span>
      </div>
  </div>
</template>
<style scoped>
.header {
  text-align: center;
  padding: 0 40px;
}

.list-title {
  padding: 0 20px 0 10px;
}
.icon-tag {
  margin: 30px 0 16px 0;
}

.time {
  margin: 5px 0 20px 0;
}

.status {
  float: right;
}
.storeInfo {
  padding: 0 40px;
}
a {
  color: #4a90e2;
}
</style>

<script>
export default {
  data() {
    return {
      id: null,
      storeInfo: null,
      serviceInfo: null
    }
  },
  computed: {
    isBuy () {
      if (!this.serviceInfo) {
        return false
      } else {
        return Object.keys(this.serviceInfo).length > 0
      }
    }
  },
  async mounted () {
    this.id = this.$route.query.id
    if (!this.id) {
      this.$router.go(-1)
    } else {
      const result = await this.$api.requestStoreInfo(this.id)
      this.storeInfo = result.storeInfo
      this.serviceInfo = result.serviceInfo
    }
  }
}
</script>



