<template title="licenses">
  <div class="server btn-p-b">
    <!-- <div class="layerGuide">
       <div class="imageGuide"> </div>
    </div> -->
    <Dropdown :items="$city.list" :selected="city" @select="selectCity" id-field="regionId" display-field="city" label="当前服务区域">
      <mt-button size="small" class="fn-right mint-button--radius" @click="newRegion()">
        开通新服务区
      </mt-button>
    </Dropdown>
    <div class="swipeList">
      <mt-swipe :auto="0" :continuous="false" :show-indicators="false">
        <mt-swipe-item v-for="(license, index) in licenses" :key="license.id">
          <div @click="licenseDetail(license)">
            <h4>{{license.name}}
              <span class="fn-right">{{index + 1}}
                <b>/{{licenses.length}}</b>
              </span>
            </h4>
            <!--<p v-if="license.dueDate">{{license.dueDate | date}}到期</p>
            <p v-else>未购买</p>-->
            <LicenseStatusCard :license="license" @click="licenseDetail(license)">
            </LicenseStatusCard>
          </div>
        </mt-swipe-item>
      </mt-swipe>
    </div>
    <div class="join-p">
      <mt-button type="primary" size="large" @click="purchase()">
        续费
      </mt-button>
    </div>
    <div class="searchWarp" ref="search">
      <div class="search">
        <h5 class="label">为商家开通服务</h5>
        <!-- <mt-cell v-if="selectedShop" :title="selectedShop.brandname + ' ' + selectedShop.storename">
          <i class="icon icon-tick" @click="clearSelectedShop">
          </i>
        </mt-cell> -->
        <Search ref="searchComponent"  :autofocus="true" :autoScrollToTop="true" @search="searchShop" :result="searchedShops">
          <template slot="searchItem" scope="props">
            <mt-cell :title="props.item.brandname + ' ' + props.item.storename" :label="props.item.address" @click.native="selectShop(props.item)"></mt-cell>
          </template>
        </Search>
      </div>
       <div class="list-title scope-title">
        <i class="icon"></i>可选服务</div>
      <AuthorizeList :licenses="shopLicenses || licenses" :authorizes="authorizes" @edit="editAuthorize">
      </AuthorizeList>
      <div class="join-p">
        <mt-button :type="canAuthorize ? 'primary' : 'default'" :disabled="!canAuthorize" size="large" @click="authorize()">
          开通服务
          <span>(总价: {{ authorizePrice | money }}元)</span>
        </mt-button>
      </div>
    </div>
  </div>
</template>
<script>
import { MessageBox } from 'mint-ui'
import { Dropdown, LicenseStatusCard, AuthorizeList, Search } from '@/components'

export default {
  components: {
    Dropdown, LicenseStatusCard, AuthorizeList, Search
  },
  data: () => ({
    guideImg: '../../static/userguide01.png',
    city: {},
    licenses: [],
    selectedShop: null,
    searchedShops: [],
    shopLicenses: null,
    selectAuthorize: null
  }),
  computed: {
    authorizes () {
      if (this.selectAuthorize != null) {
        return [this.selectAuthorize]
      }
      return []
    },
    canAuthorize() {
      return this.selectedShop != null &&
        this.authorizes != null &&
        this.authorizes.length > 0
    },
    authorizePrice() {
      if (this.authorizes == null) {
        return 0
      }

      const sum = this.authorizes.reduce((s, a) => {
        const shopLicense = this.shopLicenses.find(l => l.id === a.licenseId)
        const price = shopLicense.monthMoney * a.month
        return s + price
      }, 0)

      return sum
    }
  },
  methods: {
    async selectCity(city) {
      this.$city.current = city
      this.city = city
      this.licenses = await this.$api.getLicenseList(this.city)
    },
    newRegion() {
      this.$router.push({ name: 'choose-region' })
    },
    licenseDetail(license) {
      this.$router.push({
        name: 'license-detail',
        params: license
      })
    },
    purchase() {
      this.$router.push({ name: 'Repurchase' })
    },
    async searchShop(keyword) {
      if (keyword != null && keyword.length > 0) {
        this.searchedShops = await this.$api.searchShop(this.city, keyword)
      }
    },
    async selectShop(shop) {
      this.authorizes = []
      this.selectedShop = shop
      // this.$refs.searchComponent.placeholder = `${shop.brandname} ${shop.storename}`
      this.$refs.searchComponent.resultItemClick(shop)
      const result = await this.$api.getShopLicenses(this.city, shop)
      this.shopLicenses = result.serviceList
    },
    clearSelectedShop() {
      this.selectedShop = null
      this.shopLicenses = null
      this.authorizes = []
    },
    editAuthorize(newAuthorize) {
      if (newAuthorize.month === 0) {
        this.selectAuthorize = null
      } else {
        this.selectAuthorize = newAuthorize
      }

      // const authorize = this.authorizes.find(l => l.licenseId === newAuthorize.licenseId)
      // if (authorize != null) {
      //   authorize.month = month
      // } else {
      //   if (newAuthorize.month > 0) {
      //     this.authorizes.push(newAuthorize)
      //   }
      // }

    },
    async authorize() {
      const licenses = this.authorizes
        .filter(a => a.month > 0)
        .map(a => ({
          serviceId: a.licenseId,
          buyMonth: a.month
        }))
      const result = await this.$api.authorizeShopLicenses(this.city, this.selectedShop, licenses, this.authorizePrice)
      MessageBox('开通成功', '恭喜您，已成功为商家开通服务。')
      this.$router.push({ name: 'customers' })
    }
  },
  async mounted() {
    this.city = this.$city.current
    let licenses = await this.$api.getLicenseList(this.city)
    //防止开通第二个地区时，未买包而通过浏览器返回主页，这时当前city是未付款的
    let isAllNew = true
    for (let license of licenses) {
      if (!license.new) {
        isAllNew = false
      }
    }
    // debugger
    if (isAllNew) {
      this.$city.current = this.$city.list[0]
      this.city = this.$city.current
      this.licenses = await this.$api.getLicenseList(this.city)
    } else {
      this.licenses = licenses
    }
  }
}
</script>
<style lang="scss" scoped>
.scope-title {
  padding-left: 10px;
}
