<template>
  <div class="server-tabbar">
    <div class="server-wrap">
      <router-view></router-view>
    </div>
    <div class="mint-tabbar is-fixed">
      <a
        class="mint-tab-item"
        :class="{ 'is-selected': selected === tab }"
        v-for="tab in tabs"
        :key="tab.path"
        @click="goTab(tab)">
        <div class="mint-tab-item-icon">
          <i class="icon" :class="tab.icon"></i>
        </div>
        <div class="mint-tab-item-label">
          {{tab.label}}
        </div>
      </a>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      selected: null,
      tabs: []
    }
  },
  methods: {
    goTab(tab) {
      this.$router.push({name: tab.name})
      this.selected = tab
    }
  },
  beforeMount() {
    const allRouteConfigs = this.$router.options.routes
    const tabRoute = this.$route.matched[0]
    const tabRouteConfig = allRouteConfigs.find(route => route.path === tabRoute.path)
    const children = tabRouteConfig.children

    if (Array.isArray(children) && children.length > 0) {
      this.tabs = children.map(child => ({
        name: child.name,
        label: child.meta.label,
        icon: child.meta.icon
      }))

      if (this.$route.matched.length === 1) {
        // 路由在Tab级的情况下，进入第一个Tab
        this.$router.replace({name: children[0].name})
        this.selected = this.tabs[0]
      } else {
        // 点亮子级路由的图标
        this.selected = this.tabs.find(tab => this.$route.matched[1].name === tab.name)
      }
    }
  }
}
</script>




