<template title="我的服务">
  <div class="service btn-p-b">
    <div class="topbanner" v-if="!entryId">
      <div class="icon"><img src="~@/assets/icon_service.png" width="80" alt=""></div>
      <h1>选择你的服务</h1>
      <p>恭喜您已成功选定要拓展的城市，
        <br>挑选适合的服务版本，为商家提供精准专业的定制。</p>
    </div>
    <PurchaseList :purchases="purchases" :licenses="licenses" @edit="purchaseEdit">
    </PurchaseList>
    <div class="footer-btnWrap">
      <mt-button size="large" :type="canProceed ? 'primary' : 'default'" :disabled="!canProceed" @click="confirm()">
        购买
      </mt-button>
    </div>
  </div>
</template>

<script>
  import {
    PurchaseList
  } from '@/components'

  export default {
    components: {
      PurchaseList
    },
    data: () => ({
      entryId: null,
      licenses: [],
      purchases: []
    }),
    computed: {
      canProceed() {
        return this.purchases.find(p => !(p.quantity == 0 && p.month == 0)) != null
      }
    },
    methods: {
      purchaseEdit(newPurchase) {
        const purchase = this.purchases.find(p => p.licenseId === newPurchase.licenseId)
        if (purchase != null) {
          purchase.quantity = newPurchase.quantity
          purchase.month = newPurchase.month
          purchase.packgeId = newPurchase.packgeId
          purchase.packgeInfo = newPurchase.packgeInfo

        } else {
          if (!(newPurchase.quantity == 0 && newPurchase.month == 0)) {
            this.purchases.push(newPurchase)
          }
        }
      },
      async confirm() {
        const purchases = this.purchases
          .filter(p => !(p.quantity === 0 && p.month === 0))
          .map(p => {
            const license = this.licenses.find(l => l.id === p.licenseId)
            // return {
            //   serviceId: license.serviceId,
            //   monthMoney: 299,
            //   curEndDate: license.storeEndDate,
            //   newEndDate: p.month,
            //   curQuantity: license.totalQuantity,
            //   newQuantity: p.quantity
            // }
            return {
              licenseId: p.licenseId,
              licenseName: license.name,
              quantity: p.quantity,
              new: license.new,
              realPrice: p.price,
              month: p.month
            }
          })

        const purchasesWithName = this.purchases
          .filter(p => !(p.quantity === 0 && p.month == 0))
          .map(p => {
            console.log(p)
            const license = this.licenses.find(l => l.id === p.licenseId)
            return {
              licenseId: p.licenseId,
              licenseName: license.name,
              price: p.price,
              description: license.title,
              quantity: p.quantity,
              new: license.new,
              month: p.month
            }
          })
        const packgeList = this.purchases
          .filter(p => !(p.quantity === 0 && p.month === 0))
          .map(p => {
            p.packgeInfo.realPrice = p.packgeInfo.price
            return p.packgeInfo
          })
        // const result = await this.$api.getPurchasePackages(this.$city.current, purchases)
        // const result = await this.$api.calcPackage(purchases)
        this.$router.push({
          name: 'purchase-confirm',
          params: {
            purchases: purchasesWithName,
            packages: packgeList
          }
        })
      }
    },
    async mounted() {
      this.entryId = this.$route.query.id || null
      let licenses = await this.$api.getLicenseList(this.$city.current)
      this.licenses = licenses.filter(l => l.new)
      if (this.entryId) {
        this.licenses = licenses.filter(l => l.id == this.entryId)
      }
    }
  }
</script>
<style>
</style>




