<template>
  <dd>
    <span
      v-if="modified"
      class="btn-set modify">
      {{authorize.month}}个月
      <i class="icon"></i>
    </span>
    <span
      v-else
      class="btn-set">
      开通/续期
      <i class="icon"></i>
    </span>
  </dd>
</template>

<script>
export default {
  props: {
    authorize: Object
  },
  computed: {
    modified() {
      return this.authorize != null &&
        this.authorize.month > 0
    }
  }
}
</script>



