<template title="服务中的门店信息">
  <div class="mint-cell">
    <div class="mint-cell-wrapper">
      <div class="mint-cell-title">
        <span class="mint-cell-text">
          <h4>
            <i
              class="icon-tag"
              :class="license.inUse ? 'tag-blueBd' : 'tag-grayBd'"
              v-if="license.inUse"
              v-for="license in customer.serviceList"
              :key="license.serviceId">
              {{license.serviceName}}
            </i>
            {{customer.brandname}}
            {{customer.storename}}
          </h4>
        </span>
        <span class="mint-cell-label">到期日:{{customer.storeEndDate}}</span>
      </div>
      <div
        v-if="customer.status === 'WARNING'"
        class="mint-cell-value is-link">
        <span class="warn">即将到期</span>
      </div>
    </div>
    <i class="mint-cell-allow-right"></i>
  </div>
</template>

<script>
export default {
  props: {
    customer: Object
  }
}
</script>



