<template>
  <div class="serverDetail btn-p-b">
    <LicenseStatusCard :license="license"></LicenseStatusCard>
    <div class="list-wrap">
      <div class="list-title" v-if="customerUsages.length != 0">
        <i class="icon"></i>使用该服务的客户</div>
      <div class="list-time">
        <LicenseUsage v-for="customerUsage in customerUsages" :key="customerUsage.id" :usage="customerUsage" @click="customerDetail(customerUsage)">
        </LicenseUsage>
      </div>
    </div>
    <div v-if="customerUsages.length == 0" class="no-customer fn-center">
      <span style="display: inline-block;">
        <i class="icon"></i>
        <p>还没有使用该版本服务的客户哦！<br> 快马加鞭填满整个宇宙吧</p>
      </span>
    </div>
    <div class="footer-btnWrap">
      <mt-button type="primary" size="large" @click="purchase()">
        {{license.new  ? '购买' : '续费'}}
      </mt-button>
    </div>
  </div>
</template>
<script>
import { LicenseStatusCard, LicenseUsage } from '@/components'
import { Toast } from 'mint-ui'

export default {
  components: {
    LicenseStatusCard, LicenseUsage
  },
  data: () => ({
    license: {},
    licenseId: null,
    customerUsages: []
  }),
  methods: {
    customerDetail(customerUsage) {
      this.$router.push({
        name: 'customer-detail',
        params: { id: customerUsage.storeId }
      })
    },
    purchase() {
      if (this.license.usageStatus !== 'NONE' && this.license.totalMonth === 36 && this.license.totalQuantity === 120) {
        Toast({message: '您的用量已经达到最大值，赶紧联系更多的门店吧！'})
      } else {
        if (this.license.usageStatus === 'NONE') {
          this.$router.push({ name: 'purchase', query: {id: this.licenseId} })
        } else {
          this.$router.push({ name: 'Repurchase', query: {id: this.licenseId} })
        }
      }
    }
  },
  async mounted() {
    this.license = this.$route.params
    this.licenseId = this.license.id
    const result = await this.$api.requestServiceStores(this.$city.current.regionId, this.license.id)
    this.customerUsages = result.storeList
    this.license = result.moneyInfo
    document.title = this.license.name || '壹向·加盟商'
  }
}
</script>
<style lang="scss">
</style>



