<template>
  <i class="icon-tag" :class="isNormal ? 'tag-blueBg' : 'tag-redBg'">
    {{ isNormal ? '用量正常' : '用量告急' }}
  </i>
</template>

<script>
export default {
  props: {
    status: String
  },
  computed: {
    isNormal() {
      return this.status === 'NORMAL'
    }
  }
}
</script>



