<template title="登录手机">
  <div class="login">
    <div class="topbanner">
      <img src="~@/assets/logo.png" height="40">
    </div>
    <div class="lift-m-tb login-form">

      <mt-field placeholder="手机号" type="tel" v-model="mobile" :attr="{ maxlength: 11 }"></mt-field>
      <mt-field placeholder="验证码" v-model="code" :attr="{ maxlength: 6 }">
        <mt-button type="default" size="small" :disabled="smsBtnDisabled" @click="sendVerifyCode()">
          {{smsBtnText}}
        </mt-button>
      </mt-field>
    </div>
    <div class="page-button">
      <mt-button size="large" :type="registerBtnDisabled ? 'default' : 'primary'" :disabled="registerBtnDisabled" @click="register()">
        注册
      </mt-button>
    </div>
    <p class="join-note">轻触注册按钮，即表示您同意
      <a>壹向智慧咖啡馆服务协议</a>
    </p>
  </div>
</template>

<script>
import { login } from '@/mixin'

export default {
  mixins: [login],
  methods: {
    async register() {
      await this.$api.register(this.mobile, this.code)
      this.$router.push({ name: 'choose-region' })
    }
  },
  mounted() {
  }
}
</script>

<style>
body{
    background-color: #fff;
  }
</style>



