<template title="给商家开通">
  <div class="choose-package" v-if="license != null">
    <div class="title">
      <div class="left">
        <h4>{{license.name}}</h4>
         <p class="intr">当前到期日: {{license.endDate | date}}</p>
      </div>
      <mt-button size="small" class="fn-right mint-button--radius" @click="remove()">
        {{license.remainingMonth === 0?'不需要开通':'不需要续期'}}
      </mt-button>
    </div>
    <p class="intr">
      {{license.description}}
    </p>
    <div class="time">
      <ul>
        <li v-for="m in license.buyMonthList" :key="m" :class="{'selected': m.month === month,'disabled':license.canBuyMonth < m.month}" @click="selectMonth(m.month)">
          <span class="item">{{m.month}}个月 <br><span class="price">￥{{m.price.toFixed(2)}}</span></span>

        </li>
      </ul>

    </div>
     <div class="renew-date" v-if="license.remainingMonth != 0">续期后到期日：{{newEndDate | date}}</div>
    <div class="join-p">
      <mt-button type="primary" size="large" @click="confirm()">
        确定
      </mt-button>
    </div>
  </div>
  <div v-else>
  </div>
</template>

<script>
  import moment from 'moment'
  export default {
    props: {
      license: Object,
      authorize: Object
    },
    data: () => ({
      month: 0
    }),
    computed: {
      newEndDate () {
        let endMonth = moment(this.license.endDate || '').add(this.month, 'M')
        return endMonth
      }
    },
    watch: {
      authorize() {
        if (this.authorize != null) {
          this.month = this.authorize.month
        } else if (this.license != null) {
          this.month = this.license.buyMonthList[0].month
        } else {
          this.month = 0
        }
      }
    },
    methods: {
      selectMonth(month) {
        if (this.license.canBuyMonth < month) {
          return
        }
        this.month = month
      },
      remove() {
        this.$emit('save', {
          licenseId: this.license.id,
          month: 0
        })
      },
      confirm() {
        this.$emit('save', {
          licenseId: this.license.id,
          month: this.month
        })
      }
    }
  }
</script>



