<template>
  <div class="list-wrap">
    <div class="list-service">
      <div v-for="license in licenses" :key="license.id" @click="openEditPopup(license)">
        <dl class="item">
          <dt>
            <h4>{{license.name}}</h4>
            <p v-if="license.new">
              {{license.monthMoney | money}}/月/单个授权
            </p>
            <p v-else>
              当前用量：{{license.remainingQuantity}}个授权 | {{license.totalMonth}}个月
            </p>
          </dt>
          <PurchaseStatus :purchase="purchase(license.id)" :isRenew="license.new">
          </PurchaseStatus>
        </dl>
        <p class="intr">{{license.description}}</p>
      </div>
    </div>
    <mt-popup v-model="popupVisible" position="bottom">
      <EditPurchasePopup :license="editLicense" :purchase="editPurchase" @save="savePurchase">
      </EditPurchasePopup>
    </mt-popup>
  </div>
</template>

<script>
  import PurchaseStatus from './PurchaseStatus'
  import EditPurchasePopup from './EditPurchasePopup'

  export default {
    components: {
      PurchaseStatus,
      EditPurchasePopup
    },
    props: {
      licenses: Array,
      purchases: Array
    },
    data: () => ({
      popupVisible: false,
      editLicense: null,
      editPurchase: null,
    }),
    methods: {
      purchase(licenseId) {
        return this.purchases.find(p => p.licenseId === licenseId)
      },
      openEditPopup(license) {
        const purchase = this.purchases.find(p => p.licenseId === license.id)

        this.editLicense = license
        this.editPurchase = purchase
        this.popupVisible = true
      },
      savePurchase(purchase) {
        this.$emit('edit', purchase)
        this.popupVisible = false
        this.editLicense = null
        this.editPurchase = null
      }
    }
  }
</script>



