<template>
  <div
    class="item"
    :class="{'warning': usage.status === 'WARNING'}"
    @click="click()">
    <h4>{{usage.brandName + usage.storeName}}</h4>
    <p>
      <span class="date" v-if="usage.storeEnddate">
        {{usage.storeEnddate | date}} 服务到期
      </span>
      <span class="date" v-else>
        到期时间未知
      </span>
      <span class="timeWrap">
        <i
          v-for="i in usage.totalMonth"
          :key="i"
          :class="{normal: i <= usage.remainingMonth}">
        </i>
      </span>
    </p>
  </div>
</template>

<script>
export default {
  props: {
    usage: Object
  },
  methods: {
    click() {
      this.$emit('click')
    }
  }
}
</script>



