<template>
  <div class="landing">
    <p>{{message}}</p>
  </div>
</template>

<style lang="scss" scope>
.landing {
  display: flex;
  flex-direction: row;
  justify-content: center;
  text-align: center;
  align-items: center;
  margin: 0 auto;
  height: 100vh;
}
</style>

<script>
import wx from 'weixin-js-sdk'
export default {
  data() {
    return {
      message: '正在加载~~~'
    }
  },
  async mounted() {
    // 从wp项目返回的微信一次性认证code
    // const code = this.$route.query.token
    let str = location.search.replace(/\?/g, "")
    let code = str.split('=')[1]
    console.log(location)
    // FOR DEBUG:
    // this.$router.replace({name: 'portal'})
    // return
    /* eslint-disable no-unreachable */
    // END DEBUG:

    if (typeof code !== 'string' || code.length === 0) {
      this.message = '无法获取身份信息，请重新进入'
    } else {
      //获取用户信息之后再配置微信
      const result = await this.$api.wxConfig(location.origin)
      const timeStamp = result.timestamp + '';
      const jsTk = result.signature;
      const url = result.url;
      const appId = result.appid;
      const nStr = result.noncestr;
      const config_p = {
        debug: false, // 开启调试模式,调用的所有api的返回值会在客户端alert出来，若要查看传入的参数，可以在pc端打开，参数信息会通过log打出，仅在pc端时才会打印。
        timestamp: timeStamp, // 必填，生成签名的时间戳
        nonceStr: nStr, // 必填，生成签名的随机串
        appId: appId,
        signature: jsTk,// 必填，签名，见附录1 jsapitk
        jsApiList: ['chooseWXPay'] // 必填，需要使用的JS接口列表，所有JS接口列表见附录2
      }
      // console.log(config_p);
      wx.config(config_p)
      wx.error(function (res) {
      })
      wx.ready(function () {
      })
      // 先检查用户是否存在
      const checkResult = await this.$api.checkAutoLogin(code)
      if (checkResult.UserExist) {
        // 使用微信code登录
        const loginResult = await this.$api.loginByWx(code)

        if (loginResult.regionList != null && loginResult.regionList.length > 0) {
          this.$city.list = loginResult.regionList
          this.$city.current = loginResult.regionList[0]
          this.$router.replace({ name: 'portal' })
        } else {
          this.$router.replace({ name: 'choose-region' })
        }
      } else {
        this.$router.replace({ name: 'join-letter' })
      }
    }
  }
}
</script>



