<template title="客户首页">
  <div class="customer btn-p-b">
    <Dropdown
      :items="customerTypes"
      :selected="customerType"
      @select="selectCustomerType"
      id-field="type"
      display-field="name">
      <mt-button
        v-if="customerType.type === 'CONTACTING'"
        size="small"
        class="fn-right mint-button--radius">
        添加洽谈中客户
      </mt-button>
    </Dropdown>
    <Dropdown
      v-if="customerType.type === 'IN_SERVICE' && !searching"
      :items="licenseTypes"
      :selected="licenseType"
      @select="selectLicenseType"
      id-field="id"
      display-field="name"
      class="serve">
      <i class="icon icon-search" @click="openSearch"></i>
    </Dropdown>
    <div class="mint-searchbar" v-if="searching">
      <div class="mint-searchbar-inner">
        <input ref="search" type="search" v-model="keyword" placeholder="输入名称定位门店" class="mint-searchbar-core">
      </div>
      <a class="mint-searchbar-cancel" @click="closeSearch">
        取消
      </a>
    </div>
    <div
      v-if="customerType.type === 'IN_SERVICE'"
      class="storeList">
      <CustomerInService
        v-for="customer in filteredCustomers"
        :key="customer.id"
        :customer="customer"
        @click.native="customerDetail(customer)">
      </CustomerInService>
      <div v-if="filteredCustomers.length == 0" class="no-customer fn-center">
        <span style="display: inline-block;">
          <i class="icon"></i>
          <p>还没有客户哦！<br> 快马加鞭填满整个宇宙吧</p>
        </span>
      </div>
    </div>
    <div
      v-if="customerType.type === 'CONTACTING'"
      class="storeList">
      <mt-cell title="壹向轻食 杭大路店" value="138 1111 0000" label="杭州市教工路18号欧美中心1号楼D区106室"></mt-cell>
    </div>
  </div>
</template>

<script>
  import { Dropdown, CustomerInService } from '@/components'

  export default {
    components: {
      CustomerInService,
      Dropdown
    },
    data: () => ({
      customerType: {},
      customerTypes: [
        {
          type: 'IN_SERVICE',
          name: '服务中的客户'
        }
        // {
        //   type: 'CONTACTING',
        //   name: '洽谈中的客户'
        // }
      ],
      licenseType: {},
      licenseTypes: [],
      customers: [],
      searching: false,
      keyword: ''
    }),
    computed: {
      filteredCustomers() {
        if (this.searching) {
          return this.customers.
            filter(c => c.brandname.includes(this.keyword) ||
              c.storename.includes(this.keyword))
        }

        if (this.licenseType != null && this.licenseType.id > 0) {
          return this.customers
            .filter(c => c.serviceList.find(s =>
              s.serviceId === this.licenseType.id &&
              s.inUse) != null)
        }
        return this.customers
      }
    },
    methods: {
      selectCustomerType(type) {
        this.customerType = type
      },
      selectLicenseType(type) {
        this.licenseType = type
      },
      openSearch(e) {
        this.searching = true
        console.log(this.$refs.search)
        setTimeout(() => this.$refs.search.focus(), 200)
        if (e != null) {
          e.stopPropagation()
        }
      },
      closeSearch() {
        this.searching = false
        this.keyword = ''
      },
      customerDetail(customer) {
        this.$router.push({
          name: 'customer-detail',
          params: { id: customer.id }
        })
      }
    },
    async mounted() {
      this.customerType = this.customerTypes[0]

      this.licenseTypes = [ {id: 0, name: '全部服务'} ]
      this.licenseTypes = this.licenseTypes.concat(await this.$api.getLicenseList(this.$city.current))
      this.licenseType = this.licenseTypes[0]

      const customers = await this.$api.getCustomerList(this.$city.current)
      this.customers = customers.sort((a, b) => {
        if (a.status > b.status) {
          return -1;
        } else if (a.status < b.status) {
          return 1;
        } else {
          return 0;
        }
      })
    }
  }
</script>

<style scoped>
</style>



