<template>
  <div ref="self" class="search">
    <div class="mint-searchbar" v-show="searching">
      <div class="mint-searchbar-inner">
        <input ref="search" @click="searching = true" type="search" v-model="currentValue" :placeholder="placeholder" class="mint-searchbar-core">
      </div>
      <a class="mint-searchbar-cancel" @click="closeSearch" v-show="searching" v-text="cancelText">
      </a>
    </div>

    <mt-cell :title="searchTitle" v-show="!searching" ref="cell" @click.native="scrollToTop">
      <i class="icon icon-search"></i>
    </mt-cell>
    <div class="resultList" v-if="searching" >
      <slot name="searchItem" v-for="(item, index) in searchResult"  :item="item"></slot>
    </div>
  </div>
</template>

<script>
import debounce from 'lodash.debounce'

const scrollTop = (offsetTop) => {
  document.body.scrollTop = offsetTop
  // Firefox
  document.documentElement.scrollTop = offsetTop
  // Safari
  window.pageYOffset = offsetTop
}

export default {
  props: {
    result: Array,
    cancelText: {
      default: '取消'
    },
    placeholder: {
      default: '输入品牌定位门店'
    },
    autoScrollToTop: Boolean,
    autofocus: Boolean
  },
  data: () => ({
    searchTitle:'输入品牌定位门店',
    currentValue: '',
    searchResult: [],
    searching: false
  }),
  watch: {
    result() {
      this.searchResult = this.result
    },
    searching() {
      setTimeout(() => {
        this.autofocus && this.$refs.search.focus()
      }, 200)
    },
    currentValue(val) {
      this.emitSearchEventLazy()
    }
  },
  methods: {
    emitSearchEvent() {
      this.$emit('search', this.currentValue)
    },
    scrollToTop() {
      this.searching = true
      if (!this.autoScrollToTop) return
      let offsetTop = this.$refs.self.offsetTop
      scrollTop(offsetTop)
    },
    closeSearch() {
      this.searching = false
      this.currentValue = ''
      this.searchResult = []
    },
    resultItemClick(shop) {
      this.closeSearch()
      this.searchTitle = `${shop.brandname} ${shop.storename}`
    }
  },
  created() {
    this.emitSearchEventLazy = debounce(this.emitSearchEvent, 700)
  }
}
</script>

<style lang="scss" scoped>
.search {
  height: 50px;
  position: relative;
  z-index: 1;
}
.resultList {
  width: 100%;
  position: absolute;
  background: #fff;
  box-shadow: 0 3px 15px 0px rgba(0, 0, 0, 0.3);
  max-height: 240px;
  overflow: scroll;
}

.iconWarp {
  height: 100%;
  width: 40px;
  position: absolute;
  right: 0px;
  bottom: -10px;
}
</style>



