<template>
  <dd>
    <span v-if="modified" class="btn-set modify">
      {{purchase.quantity}}个授权 |
      {{purchase.month+(isRenew==true?'个月':'')}}
      <i class="icon"></i>
    </span>
    <span v-else class="btn-set">
      设置授权数 | 时长
      <i class="icon"></i>
    </span>
  </dd>
</template>

<script>
export default {
  props: {
     isRenew:{
      default:true
    },
    purchase: Object
  },
  computed: {
    modified() {
      return this.purchase != null &&
        !(this.purchase.month == 0 && this.purchase.quantity == 0)
    }
  }
}
</script>



