<template>
  <div
    class="serverWrap"
    :class="{'normal': license.usageStatus === 'NORMAL', 'warn': license.usageStatus === 'WARNING', 'gray': license.usageStatus === 'NONE'}">
    <ul class="moneyInfo fn-flex">
      <li>
        <span>{{license.earnedProfit | money}}</span>
        <p>已获收益(元)</p>
      </li>
      <li>
        <span>{{license.expectedProfit | money}}</span>
        <p>预期收益(元)</p>
      </li>
      <li>
        <span>{{license.cost | money}}</span>
        <p>成本投入(元)</p>
      </li>
    </ul>
    <div class="packageInfo">
      <i class="icon-tag tag-noBuy" v-if="license.usageStatus == 'NONE'">未购买 </i>
      <UsageStatusTag v-if="license.usageStatus !== 'NONE'" :status="license.usageStatus"></UsageStatusTag>
      <dl class="number">
        <dt>
          <i class="icon icon-number"></i>
        </dt>
        <dd>
          <div>
            <span>可售授权数：<b>{{license.remainingQuantity}}</b>个</span>
            <span class="total">{{license.totalQuantity}}</span>
          </div>
          <mt-progress :value="license.remainingQuantity / license.totalQuantity * 100" :bar-height="8"></mt-progress>
        </dd>
      </dl>
      <dl class="time">
        <dt>
          <i class="icon icon-time"></i>
        </dt>
        <dd>
          <div>
            <span>到期日：<b>{{license.dueDate | date}}</b></span>
            <!-- <span class="total">{{license.totalMonth}}</span> -->
          </div>
          <mt-progress :value="license.totalMonth / license.totalMonth * 100" :bar-height="8"></mt-progress>
        </dd>
      </dl>
    </div>
  </div>
</template>

<script>
import UsageStatusTag from './UsageStatusTag'

export default {
  components: {
    UsageStatusTag
  },
  props: {
    license: Object
  }
}
</script>



