<template title="加盟商购买">
  <div class="choose-package" v-if="license != null">
    <div class="title">
      <div class="left">
        <h4>{{license.name}}</h4>
        <p class="intr">
          299.00/月/单个授权
        </p>
      </div>
      <mt-button size="small" class="fn-right mint-button--radius" @click="remove()">
        不需要此服务
      </mt-button>
    </div>

    <div class="mint-radiolist">
      <a class="mint-cell" v-for="option in license.options || []" :key="option.quantity">
        <div class="mint-cell-wrapper">
          <div class="mint-cell-title">
            <label class="mint-radiolist-label">
              <span class="mint-radio">
                <input type="radio" class="mint-radio-input" v-model="quantity" :value="option.quantity || option" />
                <span class="mint-radio-core"></span>
              </span>
              <div>
                <!-- ${option.monthOptions[0].month} -->
                <h4 class="mint-cell-text">{{`${option.quantity}个授权 | ${option.monthOptions[0].month}个月`}}</h4>
                 <p class="mint-cell-label">{{`可为${option.quantity}家门店提供一年${license.name}服务`}}</p>
              </div>
            </label>
          </div>
          <div class="mint-cell-value " :class="{'checked':quantity == option.quantity}">
            <span>￥{{option.monthOptions[0].price | money}}</span>
          </div>
        </div>
      </a>
    </div>

    <div class="join-p">
      <mt-button type="primary" size="large" @click="confirm()">
        确定
      </mt-button>
    </div>
  </div>
  <div v-else>
  </div>
</template>

<style lang="scss">
.mint-radiolist-label {
  padding-left: 32px;
  padding-top: 10px;
  padding-bottom: 10px;
  -webkit-box-align: center;
  -ms-flex-align: center;
  align-items: center;
  box-sizing: border-box;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  .mint-radio {
    margin-left: -32px;
    margin-right: 10px;
  }
}

.mint-cell-value.checked {
  color:#e13f2c;
}
</style>

<script>
export default {
  props: {
    license: Object,
    purchase: Object
  },
  data: () => ({
    packgeId: null,
    quantity: 0,
    price: 0,
    month: 0
  }),
  watch: {
    quantity(val) {
      if (this.license == null || val == null) {
        return 0
      }
      const option = this.license.options.find(p => p.quantity == val)
      this.packgeId = option.monthOptions[0].packgeid
      this.month = option.monthOptions[0].month
      this.price = option.monthOptions[0].price
      // this.$emit('input', val);
    },
    purchase() {
      if (this.purchase != null) {
        this.quantity = this.purchase.quantity
      } else if (this.license != null) {
        const quantityOption = this.license.options[0]
        this.quantity = quantityOption.quantity
      } else {
        this.quantity = 0
        this.month = 0
        this.packgeId = null
      }
    }
  },
  methods: {
    remove() {
      this.$emit('save', {
        licenseId: this.license.id,
        quantity: 0,
        month: 0,
        packgeId: 0
      })
    },
    confirm() {
      const packgeInfo = this.license.options.find(o => o.quantity == this.quantity)
      this.$emit('save', {
        licenseId: this.license.id,
        quantity: this.quantity,
        month: this.month,
        price: this.price,
        packgeId: this.packgeId,
        packgeInfo: packgeInfo.monthOptions[0]
      })
    }
  }
}
</script>



