<template title="续费列表">
  <div class="Repurchase  btn-p-b">
    <div class="list-service page-part">
      <div class="" v-for="license in licenses" :key="license.id" >
        <dl class="item">
          <dt>
            <h4>{{license.name}}</h4>
            <p>当前可用授权数: {{license.remainingQuantity}}个 </p>
            <p> 到期日: {{license.dueDate}}</p>
          </dt>
          <dd>
            <span class="btn-set" :class="{'modify': licenseIsChoose(license.serviceId)}" @click="addLicenses(license)">
              {{addLicensesNum(license.serviceId)}}
              <i class="icon"></i>
            </span>
          </dd>
        </dl>
        <mt-cell title="请选择您期望的到期日" @click.native="startPickerDate(license)" is-link class="choose-date">
          <span>{{endDateFilter(license.serviceId) | date}}</span>
        </mt-cell>
        <div class="time-progress">
          <span class="progress-wrap"></span>
          <span class="current" :style="`width:${currentProgressCalc (license.serviceId)}`"></span>
          <span class="want" :style="`width:${wantProgressCalc (license.serviceId)}`"></span>
          <span class="reality" v-if="wantProgressCalc (license.serviceId, 'auto') != '0%'" :style="`width:${wantProgressCalc (license.serviceId, 'auto')}`"><i></i></span>
        </div>
        <ul class="progress-label">
          <li class="current">
            <i class="icon"></i>
            <span>当前时长</span>
          </li>
          <li class="want"  v-if="isConfiged(license.serviceId)">
            <i class="icon"></i>
            <span>期望时长</span>
          </li>
          <li class="reality" v-if="isConfiged(license.serviceId)">
            <i class="icon"></i>
            <span>充值到期日: {{autoDate(license.serviceId) | date}}</span>
          </li>
        </ul>
      </div>
      <mt-datetime-picker
          v-model="pickerDate"
          type="date"
          ref="datePicker"
          :startDate = "pickerStartDate"
          :endDate = "pickerEndDate"
          year-format="{value} 年"
          month-format="{value} 月"
          date-format="{value} 日"
          @confirm="datePickerOk">
        </mt-datetime-picker>
        <LicenseCountPicker
          ref="licensePicker"
          :minNum="0"
          :maxNum="licenseMaxCount"
          @confirm="licenseCountPickerOk">
          </LicenseCountPicker>
    </div>

    <div class="list-wrap">
      <div class="list-title" v-if="serviceList.length > 0">
      <i class="icon"></i>充值服务配置</div>
      <div class="mint-radiolist">
        <a class="mint-cell" v-for="(service , index) in serviceList" :key="index">
          <div class="mint-cell-wrapper">
            <div class="mint-cell-title">
              <label class="mint-radiolist-label">
                <span class="mint-radio">
                  <input type="radio" class="mint-radio-input" v-model="selectServiceIndex" :value="index" />
                  <span class="mint-radio-core"></span>
                </span>
                <div>
                  <h4 class="mint-cell-text">{{index === 0?'优选方案':'超值方案'}}</h4>
                  <p class="mint-cell-label" v-for="package in service.packgeList">{{ package.packgeName }} x{{package.count}}</p>
                </div>
              </label>
            </div>
            <div class="mint-cell-value radio-value" :class="{'checked':index == selectServiceIndex}">
              <span>￥{{caclServicePrice(service.packgeList) | money}}</span>
            </div>
          </div>
        </a>
      </div>
    </div>

    <div class="footer-btnWrap">
      <mt-button size="large" :type="purchases.length > 0 ? 'primary' : 'default'" :disabled="purchases.length===0" @click="confirm()">
        提交订单
      </mt-button>
    </div>
  </div>
</template>

<style lang="scss" scoped>
.list-service {
  background-color: #fff;
  .item {
    padding-left: 15px;
  }
}
</style>
<style lang="scss">
.choose-date{
  .mint-cell-wrapper{
    color: #999;
    font-size:13px!important;
  }
  .mint-cell-value{
    color:#4a90e2;
  }
}

</style>
<script>
import { LicenseCountPicker } from '@/components/'
import { Toast } from 'mint-ui'
import moment from 'moment'
let now = moment()
let threeYear = moment().subtract(3, 'year').startOf('day')
let threeYearsDays = now.diff(threeYear, 'days')
export default {
  data: () => ({
    entryId: null,
    licenseMaxCount: 0,
    pickerStartDate: new Date(),
    pickerEndDate: new Date(),
    pickerDate: null,
    licenses: [],
    selectLicense: null,
    configs: [],
    selectServiceIndex: 0,
    serviceList: []
  }),
  components: {
    LicenseCountPicker
  },
  computed: {
    purchases () {
      if (this.serviceList.length === 0) {
        return []
      }
      return this.serviceList[this.selectServiceIndex].serviceBuyInfo
    }
  },
  watch: {
    serviceList (val) {
      this.selectServiceIndex = 0
    }
  },
  methods: {
    autoDate (serviceId) {
      if (this.purchases.length === 0) {
        return ''
      }
      const purchase = this.purchases.find(p => p.serviceId === serviceId)
      if (purchase) {
        return purchase.autoEndDate
      }
      return ''
    },
    // 到期时长进度
    currentProgressCalc (serviceId) {
      const license = this.licenses.find(l => l.serviceId === serviceId)
      if (!license) {
        return '0%'
      }
      let end = moment(license.dueDate)
      let start = moment()
      let diff = end.diff(start, 'days')
      return (diff / threeYearsDays) * 100 + '%'
    },
    // 到期日进度
    wantProgressCalc (serviceId, type) {
      if (this.purchases.length === 0) {
        return '0%'
      }
      const purchase = this.purchases.find(p => p.serviceId === serviceId)
      if (!purchase) {
        return '0%'
      }
      let start = moment()   // 2017-08-01
      let end = moment(purchase.newEndDate)
      if (type === 'auto') {
        end = end = moment(purchase.autoEndDate)
      }
      const diff = end.diff(start, 'days')
      // debugger
      return (diff / threeYearsDays) * 100 + '%'
    },
    // 是否配置
    isConfiged (serviceId) {
      return this.purchases.find(p => p.serviceId === serviceId)
    },
    // 计算服务价格
    caclServicePrice (service) {
      if (service) {
        return service.reduce((s, p) => s + p.realPrice * p.count, 0)
      }
      return 0
    },
    // 增加授权按钮点击事件
    addLicensesNum (serviceId) {
      const config = this.configs.find(c => c.serviceId === serviceId)
      if (config) {
        return '追加' + config.newQuantity + '个授权'
      } else {
        return '增加授权'
      }
    },
    // 用于显示增加授权按钮的class
    licenseIsChoose (serviceId) {
      return this.configs.find(c => c.serviceId === serviceId)
    },
    //
    endDateFilter (serviceId) {
      const config = this.configs.find(c => c.serviceId === serviceId)
      if (config) {
        return config.newEndDate
      }
      return ''
    },
    addLicenses(license) {
      const licenseMaxCount = 120 - license.totalQuantity
      if (licenseMaxCount === 0) {
        Toast({message: '您的授权数已达到最大值'})
      } else {
        this.licenseMaxCount = licenseMaxCount
        this.selectLicense = license
        this.$refs.licensePicker.open()
      }
    },
    startPickerDate (license) {
      if (license.totalMonth === 36) {
        Toast({message: '您的时长已达到最大值'})
      } else {
        this.pickerStartDate = new Date(license.dueDate)
        const maxDate = moment().add(3, 'y').toDate()
        this.pickerEndDate = maxDate
        this.selectLicense = license
        this.$refs.datePicker.open()
      }

    },
    datePickerOk (value) {
      let config = this.configs.find(c => c.serviceId === this.selectLicense.serviceId)
      if (config) {
        config.newEndDate = value
      } else {
        let config = {
          serviceId: this.selectLicense.serviceId,
          newQuantity: 0,
          newEndDate: value
        }
        this.configs.push(config)
      }
      this.calcPackage()
    },
    licenseCountPickerOk (value) {
      let config = this.configs.find(c => c.serviceId === this.selectLicense.serviceId)
      if (config) {
        config.newQuantity = value
      } else {
        let config = {
          serviceId: this.selectLicense.serviceId,
          newQuantity: value,
          newEndDate: this.selectLicense.dueDate
        }
        this.configs.push(config)
      }
      this.calcPackage()
    },
    async calcPackage () {
      const result = await this.$api.calcPackage(this.$city.current.regionId, this.configs)
      let serviceList = result.filter(o => o.packgeList.length > 0)
                              //  .map(s => s)
      for (let service of serviceList) {
        let packages = []
        for (const packge of service.packgeList) {
          const findPackage = packages.find(p => p.packgeId == packge.packgeId)
          if (!findPackage) {
            packge.count = 1
            packages.push(packge)
          } else {
            findPackage.count += 1
          }
        }
        service.packgeList = packages
      }
      this.serviceList = serviceList
    },
    confirm () {
      const configsWithName = this.configs.map(c => {
        const license = this.licenses.find(l => l.serviceId === c.serviceId)
        return {
          name: license.name,
          detail: c
        }
      })
      this.$router.push({
        name: 'purchase-confirm',
        params: {
          configs: configsWithName,
          purchases: this.purchases,
          packages: this.serviceList[this.selectServiceIndex].packgeList
        }
      })
    }
  },
  async mounted() {
    this.entryId = this.$route.query.id || null
    let licenses = await this.$api.getLicenseList(this.$city.current)
    this.licenses = licenses.filter(l => !l.new)
    if (this.entryId) {
      this.licenses = licenses.filter(l => l.id == this.entryId)
    }
  }
}
</script>



