<template title="更换手机号">
  <div class="ChangeMobile">
    <div class="page-part">
      <mt-field label="手机号" placeholder="请输入新手机号码" type="tel" v-model="mobile"  :attr="{ maxlength: 11 }"></mt-field>
      <mt-field label="验证码" placeholder="收到的6位验证码" type="tel" v-model="code" :attr="{ maxlength: 6 }">
        <mt-button type="default" size="small" @click="" :disabled="smsBtnDisabled" @click="sendVerifyCode()">
          {{smsBtnText}}
        </mt-button>
      </mt-field>
    </div>
    <div class="join-p">
      <mt-button size="large" :type="okBtnDisabled ? 'default' : 'primary'" :disabled="okBtnDisabled" @click="modifyMobile()">
        确认更换
      </mt-button>
    </div>
  </div>
</template>

<script>
import {login} from '@/mixin'

export default {
  mixins:[login],
  data () {
    return {

    }
  },
  methods: {
    async modifyMobile() {
      await this.$api.modifyMobile(this.mobile, this.code)
      this.$router.go(-1)
    }
  },
  mounted() {
    this.mobile = this.$route.query.mobile || '';
  }
}
</script>



